<template>
    <div class="bg">
        <div class="online-box modelManagement">
            <div class="inp-line">
                <el-dropdown>
                    <el-button size="small">
                        名称<i class="el-icon-arrow-down el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item>名称1</el-dropdown-item>
                        <el-dropdown-item>名称2</el-dropdown-item>
                        <el-dropdown-item>名称3</el-dropdown-item>
                        <el-dropdown-item>名称4</el-dropdown-item>
                        <el-dropdown-item>名称5</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
                <el-input size="small" placeholder="请输入搜索内容" style="width: 300px"></el-input>
                <el-button size="small">新增</el-button>
                <el-button size="small">导入</el-button>
                <span class="float-btn">
                    <el-button size="small" type="primary">批量删除</el-button>
                    <el-button size="small">批量导出</el-button>
                    <el-button size="small">批量部署</el-button>
                </span>

            </div>
            <el-table :data="datalist" class="dataTable">
                <el-table-column
                    type="selection"
                    width="55">
                </el-table-column>
                <el-table-column label="模型ID">
                    <template slot-scope="scope">
                        <el-button type="text" size="small">{{scope.row.modelId}}</el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="frameName" label="框架名称"></el-table-column>
                <el-table-column prop="formatName" label="模型格式"></el-table-column>
                <el-table-column prop="info" label="模型描述"></el-table-column>
                <el-table-column prop="time" label="更新时间"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="editModel(scope.row.modelId)">修改</el-button>
                        <el-button type="text" size="small" @click="deleteModel">删除</el-button>
                        <el-button type="text" size="small" @click="dialogFormVisible2=true">导出</el-button>
                        <el-button type="text" size="small">部署</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination-line">
                <el-pagination
                    @size-change="handleSizeChange"
                    background
                    @current-change="handleCurrentChange"
                    :current-page="currentPage4"
                    :page-sizes="[100, 200, 300, 400]"
                    :page-size="100"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="400">
                </el-pagination>
            </div>
            <!-- 修改模型 -->
            <el-dialog title="修改此模型信息" :visible.sync="dialogFormVisible">
                <el-form :model="form" label-width="100px">
                    <el-form-item label="当前模型ID" >
                        <span style="color:#1777FF;">{{form.modelId}}</span>
                    </el-form-item>
                    <el-form-item label="新的模型ID" >
                        <el-input class="w300" size="small" v-model="form.newModelId" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="模型描述" >
                        <el-input class="w300" size="small" v-model="form.modelDescribe" autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
                </div>
            </el-dialog>
            <!-- 导出模型 -->
            <el-dialog title="修改此模型信息" :visible.sync="dialogFormVisible2">
                <el-form :model="derive" label-width="100px">
                    <el-form-item label="网址" >
                        <span style="color:#1777FF;">{{derive.url}}</span>
                    </el-form-item>
                    <el-form-item label="文件名" >
                        <el-input class="w300" size="small" v-model="derive.newModelId" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="保存到" >
                        <el-input class="w300" size="small" v-model="derive.modelDescribe" autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible2 = false">取 消</el-button>
                    <el-button type="primary" @click="dialogFormVisible2 = false">确 定</el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
    export default {
        name: "home",
        filters: {
            modelStatusZn (val) {
                switch (val) {
                    case 0:
                        return '未开始'
                    case 1:
                        return '进行中'
                    case 2:
                        return '已完成'
                    case 3:
                        return '失败'
                    case 4:
                        return '已停止'
                }
            }
        },
        data () {
            return {
                dialogFormVisible: false, // 修改模型信息弹框
                dialogFormVisible2: false, // 导出模型
                currentPage4: 4,
                form: {
                    modelId: '',
                    newModelId: '', // 新
                    modelDescribe: '' // 模型描述
                },
                derive: {
                    url: 'https://www.baidu.com',
                    fileName: '',
                    saveUrl: ''
                },
                datalist: [
                    {
                        modelId: 'xxx_11_xx',
                        frameName: 'TensorFlow',
                        formatName: '.pkl',
                        info: '模型ID',
                        time: '2020-08-10 12:30:00'
                    },
                    {
                        modelId: 'xxx_11_xx',
                        frameName: 'TensorFlow',
                        formatName: '.pkl',
                        info: '用于基于时序数据的故障诊断',
                        time: '2020-08-10 12:30:00'
                    },
                    {
                        modelId: 'xxx_11_xx',
                        frameName: 'TensorFlow',
                        formatName: '.pkl',
                        info: '用于基于时序数据的故障诊断',
                        time: '2020-08-10 12:30:00'
                    },
                    {
                        modelId: 'xxx_11_xx',
                        frameName: 'TensorFlow',
                        formatName: '.pkl',
                        info: '用于基于时序数据的故障诊断',
                        time: '2020-08-10 12:30:00'
                    },
                    {
                        modelId: 'xxx_11_xx',
                        frameName: 'TensorFlow',
                        formatName: '.pkl',
                        info: '用于基于时序数据的故障诊断',
                        time: '2020-08-10 12:30:00'
                    },
                    {
                        modelId: 'xxx_11_xx',
                        frameName: 'TensorFlow',
                        formatName: '.pkl',
                        info: '用于基于时序数据的故障诊断',
                        time: '2020-08-10 12:30:00'
                    }
                ]
            }
        },
        methods: {
            editModel(modelId) {
                this.form.modelId = modelId
                this.dialogFormVisible = true
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
            },
            deleteModel() {
                this.$confirm('是否确认删除模型xxx及完成部署的环境资源', '提示', {
                    confirmButtonText: '确认删除',
                    cancelButtonText: '取消',
                    callback: action => {
                        this.$message({
                            type: 'info',
                            message: `action: $ { action } `
                        });
                    }
                });
            }
        }
    }
</script>

<style scoped lang="scss">
    .bg{
        padding: 20px;
    }
    .online-box{
        height: calc(100vh - 100px);
        background-color: #fff;
        border-top-left-radius: 50px;
        padding: 30px 30px 20px;
        box-sizing: border-box;
        .w300{
            width: 300px;
        }
        .inp-line{
            padding: 0px 0 10px;
            .el-input{
                margin-right: 10px;
            }
            .el-button{
                width: 100px;
            }
            .float-btn{
                float: right;
            }
        }
        .pagination-line{
            background-color: #fff;
            padding: 20px 20px 10px;
            text-align: right;
        }
        .pagination-line{
            text-align: right;
        }
    }
</style>
<style>
    .dataTable .cell{
        font-size: 12px;
    }
    .modelManagement .el-dialog{
        width: 500px;
    }
</style>



