<template>
    <div class="bg">
        <div class="plan-box">
            <div class="search-line">
                <el-input size="small" placeholder="搜索模板" class="search-inp"></el-input>
                <el-button size="small" type="primary">创建实例</el-button>
            </div>
            <div>
                <div class="plan-ul ">
                    <div class="plan-li" v-for="(item, index) in 10" v-bind:key="item">
                        <h3>模板名{{item}}</h3>
                        <div class="img-content">
                            <img :src="imgList[index]" alt="">
                        </div>
                        <div class="btn-box">
                            <el-button type="primary" size="small" style="width: 44%">从模板创建</el-button>
                            <el-button size="small" style="width: 44%">查看文档</el-button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
         name: "home",
        data () {
             return {
                 randomNum: '',
                 imgList: []
             }
        },
        methods: {
             createArr() {
                 for (var i = 0; i < 10; i++) {
                     var img = require('../../../src/assets/img/item-' + this.randomNum + '.jpg')
                     this.imgList.push(img)
                     if (this.randomNum < 20) {
                         this.randomNum++
                     } else {
                         this.randomNum = 1
                     }
                 }
             }
        },
        mounted() {
            this.randomNum = parseInt(Math.random() * (20 - 1 + 1) + 1, 10);
            console.log(this.randomNum)
            this.createArr()
        }
    }
</script>

<style scoped lang="scss">
    .bg{
        padding: 20px;
    }
    .search-line{
        padding: 20px 6px;
    }
    .search-inp{
        width: 400px;
    }
    .plan-box{
        height: calc(100vh - 100px);
        background-color: #fff;
        border-top-left-radius: 50px;
        padding: 20px 30px;
        box-sizing: border-box;
        .plan-ul{
            display: flex;
            flex-shrink:5;
            flex-flow: wrap;
            justify-content:space-around;
        }
        .plan-li{
            width: 17%;
            box-sizing: border-box;
            border: 1px solid #E0E0E0;
            box-shadow: 1px 1px 10px #E0E0E0;
            margin-bottom: 20px;
            border-radius: 4px;
            padding: 10px;
            margin-right: 10px;
            margin-left: 10px;
            h3{
                margin: 0;
                line-height: 30px;
                padding-bottom: 4px;
                font-size: 14px;
            }
            .img-content{
                width: 100%;

                img{
                    width: 100%;
                    border-radius: 4px;
                }
            }
            .btn-box{
                text-align: center;
                padding: 10px 0;
            }
        }
    }
</style>



