<template>
  <div id="app">
    <el-container>
      <el-aside class="home-menu">
        <MENU></MENU>
      </el-aside>
      <el-container class="content-container">
        <el-header class="header-box">
          <span class="login-info">
            <el-avatar size="small" icon="el-icon-user-solid"></el-avatar>
            <span>wangzhiwei3306@163.com</span>
          </span>
        </el-header>
        <el-main style="padding: 0;">
          <router-view />
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
    import getBrowserCore from "./model/getBrowserCore";
    import MENU from './components/menu'
    export default {
        name: 'App',
        components: {
            'MENU': MENU
        },
        mounted() {
            if (getBrowserCore() !== 'Google Chrome or Chromium') {
                this.$message('为了保证拖拽效果，建议使用chrome浏览器')
            }
        }
    }
</script>

<style>
  body {
    margin: 0;
  }
  /*#app {*/
  /*  font-family: 'Avenir', Helvetica, Arial, sans-serif;*/
  /*  -webkit-font-smoothing: antialiased;*/
  /*  -moz-osx-font-smoothing: grayscale;*/
  /*  text-align: center;*/
  /*  color: #2c3e50;*/
  /*  height: 100%;*/
  /*  width: 100%;*/
  /*}*/
  /*.el-loading-mask.is-fullscreen {*/
  /*  position: fixed;*/
  /*  left: 0;*/
  /*  right: 0;*/
  /*  top: 0;*/
  /*  bottom: 0;*/
  /*}*/
  /*foreignObject {*/
  /*  !* 解决foreignObject默认遮挡溢出部分导致的形变 *!*/
  /*  overflow: inherit;*/
  /*  animation-duration: 1s;*/
  /*  animation-fill-mode: both;*/
  /*  animation-name: fadeInLeft;*/
  /*}*/
  #app{
    background-color: #F7F7F8;
  }
  .home-menu{
        height: 100vh;
      width: 160px !important;
      position: relative;
      z-index: 100;
      overflow: visible;
  }
  .content-container{
    background: #F7F7F8;
  }
  .header-box{
    background: #fff;
    border-bottom-left-radius: 50px;
    box-shadow: 1px 1px 4px rgba(0,0,0,.2);
  }
  .login-info{
    float: right;
    font-size: 14px;
    margin-top: 18px;
    color: #666666;
  }
  .login-info .el-avatar{
    vertical-align: middle;
    margin-right: 4px;
  }
</style>



