                        _this.myChart_history = echarts.init(document.getElementById('myChart_along'));
                        _this.myChart_along.series.data = res.data.message
                        _this.myChart_along.xAxis.data = res.data.xAxis
                        _this.myChart_along.series.markPoint.data = [{
                            coord: [1000, res.data.message[1000]]
                        }]
                        _this.myChart_history.setOption(_this.myChart_along)
