<template>
    <div class="bg">
        <div class="online-box">
            <div class="back-line" @click="goDetail()"><span class="el-icon-arrow-left"></span>在线调试</div>
            <div class="btn-line">
                <el-button type="primary" size="small" @click="dialogTableVisible=true">状态码查询</el-button>
            </div>
            <div>
                <div class="debug-left">
                    <h3>在线调试请求参数</h3>
                    <div class="options-box">
                        <p>接口地址</p>
                        <el-input></el-input>
                        <p>Token</p>
                        <el-input></el-input>
                        <editor
                            ref="myEditor"
                            class="json-editor"
                            v-model="jsonEditor"
                            @init="editorInit"
                            :options="options"
                            lang="json"
                            theme="chrome"
                        ></editor>
                    </div>
                </div>
                <div class="debug-right">
                    <h3>调试信息</h3>
                    <div class="info-box">

                    </div>
                </div>
            </div>
            <el-dialog title="状态码查询" :visible.sync="dialogTableVisible">
                <el-table border :data="tableData">
                    <el-table-column property="value" label="状态码" width="150"></el-table-column>
                    <el-table-column property="label" label="描述信息" ></el-table-column>
                </el-table>
            </el-dialog>
        </div>
    </div>
</template>

<script>
    export default {
        name: "home",
        components: {
            editor: require('vue2-ace-editor')
        },

        filters: {
            modelStatusZn (val) {
                switch (val) {
                    case 0:
                        return '未开始'
                    case 1:
                        return '进行中'
                    case 2:
                        return '已完成'
                    case 3:
                        return '失败'
                    case 4:
                        return '已停止'
                }
            }
        },
        data () {
            return {
                dialogTableVisible: false, // 状态码查询弹框
                tableData: [
                    {
                        value: 200,
                        label: '请求成功'
                    },
                    {
                        value: 400,
                        label: '缺少信息请检查请求头、参数'
                    },
                    {
                        value: 401,
                        label: '请求路劲非法'
                    },
                    {
                        value: 404,
                        label: '未得知请求域名'
                    },
                    {
                        value: 405,
                        label: 'Token值未查到'
                    },
                    {
                        value: 500,
                        label: '参数不正确'
                    },
                    {
                        value: 504,
                        label: '后端请求超时'
                    }
                ],
                jsonEditor: JSON.stringify('//change JSON click left button show the change'),
                options: {
                    enableBasicAutocompletion: true,
                    enableSnippets: true,
                    enableLiveAutocompletion: true/* 自动补全 */
                }
            }
        },
        methods: {
            editorInit: function (editor) { // 右侧JSON相关可以忽略
                require('brace/ext/language_tools') // language extension prerequsite...
                require('brace/mode/html')
                require('brace/mode/javascript') // language
                require('brace/mode/less')
                require('brace/theme/chrome')
                require('brace/snippets/javascript') // snippet
                require('brace/mode/json');
                require('brace/theme/tomorrow');
            },
            goDetail() {
                this.$router.push('/onlineDetail')
            }
        }
    }
</script>

<style scoped lang="scss">
    .bg{
        padding: 20px;
    }
    .online-box{
        height: calc(100vh - 100px);
        background-color: #fff;
        border-top-left-radius: 50px;
        padding: 20px 30px;
        box-sizing: border-box;
        .pagination-line{
            background-color: #fff;
            padding: 20px 20px 10px;
            text-align: right;
        }
        .back-line{
            font-size: 16px;
            color: #333;
            line-height: 40px;
        }
        .btn-line{
            padding: 4px 0 16px;
        }
        h3{
            color: #333;
            font-size: 14px;
            margin: 0;
            padding: 0 0 10px 6px;
        }
        p{
            padding: 0;
            margin: 0;
        }
        .debug-left{
            width: 54%;
            float: left;
            .options-box{
                height: calc(100vh - 260px);
                border: 1px solid #ddd;
                padding: 0px 14px 14px;
                box-sizing: border-box;
                border-radius: 6px;
                p{
                    line-height: 40px;
                    font-size: 14px;
                }
            }
            .json-editor{
                margin-top: 16px;
                /*width: 100% !important;*/
                height: calc(100vh - 460px) !important;
            }
        }
        .debug-right{
            width: 45%;
            float: right;
            .info-box{
                background: #333;
                height: calc(100vh - 260px);
                border-radius: 6px;
            }
        }

    }
</style>
<style>
    .online-box .ace-chrome .ace_print-margin{
        background-color:#fff;
   }
    .online-box .cell{
        padding: 6px 10px;
    }
</style>



