<template>
    <div class="menu-box">
        <h2 class="menu-title">
            <span class="icon el-icon-s-tools" style="margin-right: 8px"></span>数据智能</h2>
        <div class="menu-container">
            <el-menu
                    default-active="1"
                    class="el-menu-vertical-demo"
                    @select="handleSelect"
            >
                <el-menu-item index="1">
                    <span slot="title">方案模板</span>
                </el-menu-item>
                <el-menu-item index="2">
                    <span slot="title">数据集管理</span>
                </el-menu-item>
                <el-submenu index="3">
                    <template slot="title">
                        <span>模型开发和训练</span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item index="3-1">数据预处理</el-menu-item>
                        <el-menu-item index="3-2">特征工程</el-menu-item>
                        <el-menu-item index="3-3">选择算法</el-menu-item>
                        <el-menu-item index="3-4">算法评估</el-menu-item>
                    </el-menu-item-group>
                </el-submenu>

                <el-menu-item index="4">
                    <span slot="title">模型管理</span>
                </el-menu-item>
                <el-menu-item index="5">
                    <span slot="title">模板部署</span>
                </el-menu-item>
            </el-menu>
        </div>
    </div>
</template>

<script>
    export default {
        methods: {
            handleSelect(key, keyPath) {
                // 根据index 判断页面跳转
                let index = keyPath[keyPath.length - 1];
                switch (index) {
                    case '1':
                        this.$router.push('/plan')
                        break
                    case '2':
                        this.$router.push('/dataManagement')
                        break
                    // case '2-2':
                    //     this.$router.push('/dataTable') // 废弃
                    //     break
                    case '3-1':
                        this.$router.push('/diagramExample')
                        break
                    case '3-2':
                        this.$router.push('/engineering')
                        break
                    case '3-3':
                        this.$router.push('/selectAlgorithm')
                        break
                    case '3-4':
                        this.$router.push('/assessAlgorithm')
                        break
                    case '4':
                        this.$router.push('/modelManagement')
                        break
                    case '5':
                        this.$router.push('/online')
                        break
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    .menu-container{
        padding-top: 50px;
        background-color: #fff;
        border-top-right-radius: 50px;
        box-shadow: 1px 1px 4px rgba(0,0,0,.2);
        height: calc(100vh - 70px);
        box-sizing: border-box;
        overflow: hidden;
    }

    .menu-title{
        padding-left: 20px;
        color: #fff;
        font-size: 18px;
        color: #464F68;
        line-height: 70px;
        margin: 0;
    }

</style>
<style>
    .el-menu{
        border-right: 0;
    }
</style>



