<!-- 随机森林 -->
<template>
    <div>
        <ul class="prop-tab ">
            <li v-for="(item, index) in nodeData" v-bind:key="index" @click="currentPage=index" :class="{active:currentPage===index}">{{item.label}}</li>
        </ul>
        <div class="prop-body">
            <div   v-for="(item, index) in nodeData" :key="index">
                <el-form :model="formData"  ref="formData">
                    <el-form-item :label="itemInp.label"  v-for="(itemInp, index2) in item.data" :key="index2" v-if="currentPage === index">
                        <el-select v-if="itemInp.type==='select'" size="small"  v-model="itemInp.value" style="width: 100%" :placeholder="itemInp.placeholder">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                        <el-input size="small" v-model="itemInp.value"  v-if="itemInp.type==='input'" :placeholder="itemInp.placeholder"></el-input>
                    </el-form-item>

                </el-form>
            </div>

        </div>
    </div>
</template>

<script>
    export default {
        name: "randomForest",
        props: ['nodeData'],
        data() {
            return {
                currentPage: 0,
                formData: {

                }
            }
        }

    }
</script>

<style scoped lang="scss">
    .prop-tab{
        list-style: none;
        clear: both;
        margin: 0;
        padding: 0;
        border-bottom: 1px solid #ddd;
        height: 36px;
        display: flex;
        li{
            flex: 1;
            font-size: 13px;
            color: #333;
            text-align: center;
            line-height: 36px;
            background-color: #eee;
            cursor: pointer;
        }
        li.active{
            background-color: #f5f5f5;
        }
    }
    .prop-body{
        padding: 10px 20px;
        box-sizing: border-box;
    }
</style>



