<template>
    <div class="bg">
        <div class="online-box dataManagement" >
            <el-button size="small" @click="openUpload" style="width: 160px;" icon="el-icon-upload">注册数据集</el-button>
<!--            <div slot="tip" class="el-upload__tip">上传本地文件仅支持txt、csv、且大小不能超过20M</div>-->
            <el-dialog title="注册数据集" v-if="dialogTableVisible" :visible.sync="dialogTableVisible">
                <el-form label-width="100px" :model="formData" :rules="rules" ref="formData">
                    <el-form-item label="表名称" prop="name">
                        <el-input size="small" v-model="formData.name" style="width: 400px"></el-input>
                    </el-form-item>
                    <el-form-item  label="上传方式" prop="type">
                        <el-radio-group  v-model="formData.type">
                            <el-radio size="small" label="导入文件"></el-radio>
                            <el-radio size="small" label="同步数据"></el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="存储路径" prop="path" v-if="formData.type==='导入文件'">
                        <el-input size="small" disabled v-model="formData.path" style="width: 400px" placeholder="请选择"></el-input>
                    </el-form-item>
                    <el-form-item label="数据库名" size="small" prop="databaseName" v-if="formData.type==='同步数据'">
                        <el-input v-model="formData.databaseName" style="width: 400px"></el-input>
                    </el-form-item>
                    <el-form-item label="表名" size="small" prop="tableName" v-if="formData.type==='同步数据'">
                        <el-input v-model="formData.tableName" style="width: 400px"></el-input>
                    </el-form-item>
                    <el-form-item label="用户名" size="small" prop="userName" v-if="formData.type==='同步数据'">
                        <el-input v-model="formData.userName" style="width: 400px"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" size="small" prop="password" v-if="formData.type==='同步数据'">
                        <el-input v-model="formData.password" style="width: 400px"></el-input>
                    </el-form-item>
                </el-form>
                <el-upload
                    class="upload-demo"
                    v-if="formData.type==='导入文件'"
                    ref="upload"
                    :before-upload="beforeAvatarUpload"
                    drag
                    :data="{
                        data_user_id:1,
                        data_name:formData.name,
                    }"
                    name="datafile"
                    :action="UPLOAD_FILE"
                    :on-success="handleAvatarSuccess"
                    :auto-upload="false"
                    multiple>
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">拖拽文件到虚线框内上传，或<em>点击上传</em>
                    <p style="padding: 0;font-size: 12px;color: #999;">支持txt、csv等类型的文件，且大小不能超过20M</p></div>
                </el-upload>
                <div class="btn-line">
                    <el-button type="primary" @click="onSubmit('formData')" size="small">提交</el-button>
                    <el-button size="small" @click="resetForm">取消</el-button>
                </div>
            </el-dialog>
            <el-dialog title="注册数参数配置" v-if="dialogTableVisible2" :visible.sync="dialogTableVisible2">
                <el-form label-width="100px" :model="formData"  ref="formData">
                    <el-form-item label="表头可视化"  >
                        <el-select size="small" v-model="formData2.set_header_input" style="width: 400px" placeholder="选择要输入的表头进行可视化">
                            <el-option :label="item" :value="item" v-for="(item, index) in headerList" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="修改表头"  >
                        <el-select size="small" v-model="formData2.set_header_type" style="width: 400px" placeholder="选择要修改表头">
                            <el-option :label="item" :value="item" v-for="(item, index) in headerList" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="唯一标识">
                        <el-select size="small" v-model="formData2.set_header_id" style="width: 400px" placeholder="选择数据集中的唯一标识">
                            <el-option :label="item" :value="item" v-for="(item, index) in headerList" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
                <div class="btn-line">
                    <el-button type="primary" @click="submitFrom" size="small">提交</el-button>
                    <el-button size="small" @click="dialogTableVisible2=false">取消</el-button>
                </div>
            </el-dialog>
            <el-table
                :data="showDataList"
                class="dataTable"
                style="width: 100%">
                <el-table-column
                    prop="date"
                    label="数据集ID/名称"
                    width="180">
                    <template slot-scope="scope">
                        <el-button type="text" size="small">{{scope.row.id}}</el-button>
                        <p class="table-p">{{scope.row.data_name}}</p>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="data_from"
                    label="数据来源"
                    width="180">
                </el-table-column>
                <el-table-column
                    prop="data_type"
                    :formatter="dataTypeZn"
                    label="存储类型">
                </el-table-column>
                <el-table-column
                    prop="data_path"
                    width="400"
                    label="数据集路径/链接信息">
                </el-table-column>
                <el-table-column
                    prop="create_time"
                    sortable
                    label="创建时间">
                    <template slot-scope="scope">
                        <span>{{scope.row.create_time | create_time}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="data_plan" label="备注"></el-table-column>
                <el-table-column
                    prop="address"
                    label="操作">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="clickDelete(scope.row.id)">删除</el-button>
                        <el-button type="text" size="small" @click="downLoadFile(scope.row.data_path)">导出数据集</el-button>
                        <el-button v-if="scope.row.data_lable===0" type="text" size="small" @click="createdTip(scope.row.id)"  style="margin-left: 0;">创建标注任务</el-button>
                        <el-button v-if="scope.row.data_lable===1" type="text" size="small" @click="goDetail" style="margin-left: 0;">标注</el-button>
                        <el-button v-if="scope.row.data_lable===1" type="text" size="small" @click="changedTip(scope.row.id)" style="margin-left: 0;">修改</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination-line">
                <el-pagination
                        @size-change="handleSizeChange"
                        background
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[10, 20, 30, 40]"
                        :page-size="pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="dataList.length">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    import {UPLOAD_FILE, ALL_DATA_LIST, DELETE_DATA_LIST, SET_HEADER, SET_OPTIONS, GET_OPTIONS} from '../../assets/url'
    import axios from 'axios'
    import moment from 'moment'
    export default {
        name: "home",
        filters: {
            modelStatusZn (val) {
                switch (val) {
                    case 0:
                        return '未开始'
                    case 1:
                        return '进行中'
                    case 2:
                        return '已完成'
                    case 3:
                        return '失败'
                    case 4:
                        return '已停止'
                }
            },

            create_time(val) {
                if (!val) return ''
                return moment(val).format("YYYY-MM-DD HH:mm:ss")
            }
        },

        data () {
            return {
                isCreate: false,
                UPLOAD_FILE: UPLOAD_FILE,
                pageSize: 10,
                currentPage: 1,
                rules: {
                    name: [
                        { required: true, message: '请输入表名称', trigger: 'blur' },
                        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                    ],
                    path: [
                        { required: true, message: '请选择存储路径', trigger: 'change' }
                    ],
                    type: [
                        { required: true, message: '请选择上传方式', trigger: 'change' }
                    ],
                    databaseName: [
                        { required: true, message: '请输入数据库名', trigger: 'blur' },
                        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                    ],
                    tableName: [
                        { required: true, message: '请输入表名', trigger: 'blur' },
                        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                    ],
                    userName: [
                        { required: true, message: '请输入用户名', trigger: 'blur' },
                        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '请输入密码', trigger: 'blur' },
                        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                    ]
                },
                headerList: [],
                formData: {
                    name: '',
                    type: '导入文件',
                    path: '/root/TCT_API_Develop/media/',
                    databaseName: '', // 数据库名
                    tableName: '', // 表名
                    userName: '', // 用户名
                    password: '' // 密码
                },
                formData2: {
                    set_header_input: '',
                    set_header_type: '',
                    set_header_id: '',
                    data_set_id: '',
                    data_user_id: '1'
                },
                fileList: [],
                dialogTableVisible: false, // 注册数据集弹窗
                dialogTableVisible2: false,
                dataList: [
                    {
                        dataId: 'd-isjadasda',
                        label: '道岔数据',
                        source: '数据同步/用户导入',
                        saveType: 'Cvs/txt/mysql',
                        linkInfo: '链接字符串或文件路径',
                        createTime: '2020-6-22 12:30:30',
                        remark: '数据已经做完了特征工程',
                        id: 1
                    },
                    {
                        dataId: 'd-isjadasda',
                        label: '道岔数据',
                        source: '数据同步/用户导入',
                        saveType: 'Cvs/txt/mysql',
                        linkInfo: '链接字符串或文件路径',
                        createTime: '2020-6-22 12:30:30',
                        remark: '数据已经做完了特征工程',
                        id: 2
                    },
                    {
                        dataId: 'd-isjadasda',
                        label: '道岔数据',
                        source: '数据同步/用户导入',
                        saveType: 'Cvs/txt/mysql',
                        linkInfo: '链接字符串或文件路径',
                        createTime: '2020-6-22 12:30:30',
                        remark: '数据已经做完了特征工程',
                        id: 3
                    }
                ]
            }
        },
        computed: {
            showDataList: function() {
                return this.dataList.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
            }
        },
        mounted() {
           this.getAllData()
        },
        methods: {
            dataTypeZn (row, column) {
                switch (row.data_type) {
                    case 1:
                        return 'csv'
                    case 2:
                        return 'text'
                    case 3:
                        return 'mysql'
                }
            },
            openUpload() {
                this.dialogTableVisible = true
                this.formData.name = ''
            },
            beforeAvatarUpload(file) {
                const isJPG = ((file.type === 'text/plain') || (file.type === 'application/vnd.ms-excel'))
                const isLt2M = file.size / 1024 / 1024 < 20;
                if (!isJPG) {
                    this.$message.error('上传文件只能是txt或cvs格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传文件大小不能超过 20MB!');
                }
                return isJPG && isLt2M;
            },
            handleAvatarSuccess(res, file) {
                // 上传成功关闭弹框
                if (res.code === 200) {
                    this.dialogTableVisible = false
                    // 更新列表数据
                    this.getAllData()
                }
            },
            getFormValue(id) {
                this.$api.get(GET_OPTIONS + '?data_set_id=' + id)
                .then((res) => {
                    this.formData2.set_header_input = res.data.all_data_lable.set_header_input
                    this.formData2.set_header_type = res.data.all_data_lable.set_header_type
                    this.formData2.set_header_id = res.data.all_data_lable.set_header_id
                    this.formData2.data_set_id = res.data.all_data_lable.data_set_id
                })
            },
            changedTip(id) {
                this.dialogTableVisible2 = true
                this.getHeaderList(id)
                this.formData2.data_set_id = id
                // 获取之前的信息
                this.getFormValue(id)
                this.isCreate = true
            },
            submitFrom() {
                // 提交数据集参数
                if (!this.isCreate) {
                    this.$api.post(SET_OPTIONS, this.formData2)
                    .then((res) => {
                        console.log(res)
                        this.dialogTableVisible2 = false
                        this.getAllData()
                    })
                } else {
                    // 更新
                    this.$api.post(GET_OPTIONS, this.formData2)
                    .then((res) => {
                        console.log(res)
                        this.dialogTableVisible2 = false
                        this.getAllData()
                    })
                }
            },
            createdTip(id) {
                this.dialogTableVisible2 = true
                this.getHeaderList(id)
                this.formData2.data_set_id = id
                this.isCreate = false
            },
            handleSizeChange(val) {
                this.pageSize = val
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.currentPage = val
                console.log(`当前页: ${val}`);
            },
            downLoadFile(str) { // 导出数据集
                window.location.href = 'http://47.95.214.123:8080' + str
            },
            clickDelete(id) {
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteItem(id)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            deleteItem(id) {
                this.$api.post(DELETE_DATA_LIST, {
                    data_user_id: '1',
                    set_id: id
                })
                .then((res) => {
                    console.log(res)
                    this.getAllData()
                })
            },
            getHeaderList(id) {
                this.$api.post(SET_HEADER, {
                    data_user_id: '1',
                    set_id: id
                })
                .then((res) => {
                    console.log(res)
                    this.headerList = res.data.set_header_list
                    // this.dataList = res.data.all_data_set
                })
            },
            getAllData() {
                this.$api.post(ALL_DATA_LIST, {
                    data_user_id: '1'
                })
                .then((res) => {
                    this.dataList = res.data.all_data_set
                })
            },
            resetForm() {
                this.dialogTableVisible = false
                this.formData = {
                    name: '',
                    type: '导入文件',
                    path: '',
                    databaseName: '', // 数据库名
                    tableName: '', // 表名
                    userName: '', // 用户名
                    password: '' // 密码
                }
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            handleExceed(files, fileList) {
                this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
            },
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$refs.upload.submit();
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            goDetail() {
                this.$router.push('/dataTableDetail')
            }
        }
    }
</script>

<style scoped lang="scss">
    .bg{
        padding: 20px;
    }
    .online-box{
        height: calc(100vh - 100px);
        background-color: #fff;
        border-top-left-radius: 50px;
        padding: 30px 30px 20px;
        box-sizing: border-box;
        .pagination-line{
            background-color: #fff;
            padding: 20px 20px 10px;
            text-align: right;
        }
        .el-upload__tip{
            color: #999;
            margin-top: 10px;
        }
        .upload-demo{
            margin-left: 100px;
        }
        .btn-line{
            padding-left: 100px;
            padding-top: 14px;
            .el-button{
                width: 100px;
            }
        }
    }
</style>
<style>
    .dataTable{
        margin-top: 10px;
    }
    .dataTable p{
        margin: 0;
        padding: 0;
    }
    .dataTable th{
        background-color: #FAFAFA;
        padding: 4px 0;
    }
    .dataTable td{
        padding: 5px 0;
    }
    .dataTable .cell{
        font-size: 12px;
    }
    .dataManagement .el-dialog{
        width: 600px;
    }
</style>



