<template>
    <div class="bg">
        <div class="online-box">
            <el-table
                    class="dataTable"
                    ref="multipleTable"
                    :data="tableData"
                    tooltip-effect="dark"
                    style="width: 100%"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        label="服务ID/名称"
                        align="center"
                        width="120">
                    <template slot-scope="scope">
                        <el-button size="small" type="text" @click.native="goDetail()">
                            {{ scope.row.serveName }}
                        </el-button>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="serveType"
                        label="服务方式"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="模型状态"
                        width="100">
                    <template slot-scope="scope">
                        <span class="model-status" :class="'model-status-'+scope.row.modelStatus"></span>{{scope.row.modelStatus|modelStatusZn}}
                    </template>
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="查看日志"
                        align="center"
                        width="120">
                    <template slot-scope="scope">
                        <span class="el-icon-folder-opened"></span>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="data"
                        label="更新时间"
                        align="center"
                        sortable
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="serveInfo"
                        label="服务说明"
                        width="200">
                </el-table-column>
                <el-table-column
                        label="操作"
                >
                    <template slot-scope="scope">
                        <el-button type="text" size="small">启动</el-button>
                        <el-button type="text" size="small">删除</el-button>
                        <el-button type="text" size="small">在线调试</el-button>
                        <el-button type="text" size="small">更多</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination-line">
                <el-pagination
                        @size-change="handleSizeChange"
                        background
                        @current-change="handleCurrentChange"
                        :current-page="currentPage4"
                        :page-sizes="[100, 200, 300, 400]"
                        :page-size="100"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="400">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "home",
        filters: {
            modelStatusZn (val) {
                switch (val) {
                    case 0:
                        return '未开始'
                    case 1:
                        return '进行中'
                    case 2:
                        return '已完成'
                    case 3:
                        return '失败'
                    case 4:
                        return '已停止'
                }
            }
        },
        data () {
            return {
                currentPage4: 4,
                tableData: [{
                    serveName: 'xxx_1111',
                    serveType: '调用信息',
                    modelStatus: 0,
                    id: 1,
                    serveInfo: '转辄机故障预测',
                    data: '2020-7-14 12:12:12'
                }, {
                    serveName: 'xxx_1111',
                    serveType: '调用信息',
                    modelStatus: 2,
                    id: 1,
                    serveInfo: '转辄机故障预测',
                    data: '2020-7-14 12:12:12'
                }, {
                    serveName: 'xxx_1111',
                    serveType: '调用信息',
                    modelStatus: 3,
                    id: 1,
                    serveInfo: '转辄机故障预测',
                    data: '2020-7-14 12:12:12'
                }, {
                    serveName: 'xxx_1111',
                    serveType: '调用信息',
                    modelStatus: 4,
                    id: 1,
                    serveInfo: '转辄机故障预测',
                    data: '2020-7-14 12:12:12'
                }, {
                    serveName: 'xxx_1111',
                    serveType: '调用信息',
                    modelStatus: 1,
                    id: 1,
                    serveInfo: '转辄机故障预测',
                    data: '2020-7-14 12:12:12'
                }, {
                    serveName: 'xxx_1111',
                    serveType: '调用信息',
                    modelStatus: 1,
                    id: 1,
                    serveInfo: '转辄机故障预测',
                    data: '2020-7-14 12:12:12'
                }, {
                    serveName: 'xxx_1111',
                    serveType: '调用信息',
                    modelStatus: 1,
                    id: 1,
                    serveInfo: '转辄机故障预测',
                    data: '2020-7-14 12:12:12'
                }, {
                    serveName: 'xxx_1111',
                    serveType: '调用信息',
                    modelStatus: 1,
                    id: 1,
                    serveInfo: '转辄机故障预测',
                    data: '2020-7-14 12:12:12'
                }, {
                    serveName: 'xxx_1111',
                    serveType: '调用信息',
                    modelStatus: 1,
                    id: 1,
                    serveInfo: '转辄机故障预测',
                    data: '2020-7-14 12:12:12'
                }, {
                    serveName: 'xxx_1111',
                    serveType: '调用信息',
                    modelStatus: 1,
                    id: 1,
                    serveInfo: '转辄机故障预测',
                    data: '2020-7-14 12:12:12'
                }]
            }
        },
        methods: {
            goDetail() {
                this.$router.push('/onlineDetail')
            },
            handleSelectionChange(val) {
                 console.log(val);
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
            }
        }
    }
</script>

<style scoped lang="scss">
    .bg{
        padding: 20px;
    }
    .online-box{
        height: calc(100vh - 100px);
        background-color: #fff;
        .pagination-line{
            background-color: #fff;
            padding: 20px 20px 10px;
            text-align: right;
        }
        .model-status{
            width: 8px;
            height: 8px;
            border-radius: 50%;
            display: inline-block;
            background-color: #666666;
            vertical-align: middle;
            margin-right: 6px;
        }
        .model-status-0{
            background-color: #DDDDDD;
        }
        .model-status-1{
            background-color: #1677FF;
        }
        .model-status-2{
            background-color: #52C41A;
        }
        .model-status-3{
            background-color: #FF3B30;
        }
        .model-status-4{
            background-color: #ebad18;
        }
    }
</style>
<style>
    .online-box .el-table td{
        padding: 6px 0;
    }
    .online-box .el-table th .cell{
        color: #666666;
    }
    .online-box .el-table th{
        background-color: #fafafa;
        font-weight: normal;
    }
    .dataTable .cell{
        font-size: 12px;
    }
</style>



