<template>
    <div class="bg">
        <div class="onlineDetail-box">
            <div class="back-line"><span class="el-icon-arrow-left"></span>道岔转辙机故障诊断</div>
            <div>
                <el-tabs v-model="activeName" @tab-click="handleClick">
                    <el-tab-pane label="服务详情" name="first">
                        <div class="btn-line">
                            <el-button size="small" @click="changeStart" v-if="isStart">停止</el-button>
                            <el-button size="small" @click="changeStart" v-if="!isStart">启动</el-button>
                            <el-button size="small" @click="goDebug">在线调试</el-button>
                            <el-button size="small" @click="dialogFormVisible=true">调用方式</el-button>
                            <el-button size="small" type="primary">删除</el-button>
                        </div>
                        <div>
                            <h3 class="online-title">基本信息</h3>
                            <ul class="online-ul">
                                <li>
                                    <span class="list-text">服务ID/名称</span>
                                    <span class="list-content">xxxx111_xxx</span>
                                    <span class="list-text">运行时长</span>
                                    <span class="list-content">125分钟</span>
                                </li>
                                <li>
                                    <span class="list-text">服务方式</span>
                                    <span class="list-content">调用信息</span>
                                    <span class="list-text">服务说明</span>
                                    <span class="list-content">转辙机故障预测</span>
                                </li>
                                <li>
                                    <span class="list-text">模型状态</span>
                                    <span class="list-content">{{isStart?'已启动':'已停止'}}</span>
                                    <span class="list-text">创建时间</span>
                                    <span class="list-content">2020-07-07 12:30:20</span>
                                </li>
                                <li>
                                    <span class="list-text">使用资源</span>
                                    <span class="progress-item">
                                        <el-progress :width="60" color="#52cca3" :show-text="false" type="circle" :percentage="25"></el-progress>
                                        <span class="progress-title">内存</span>
                                        <span class="progress-text">50M</span>
                                    </span>
                                    <span class="progress-item">
                                        <el-progress :width="60" :show-text="false" type="circle" :percentage="25"></el-progress>
                                        <span class="progress-title">CPU</span>
                                        <span class="progress-text">1</span>
                                    </span>
                                    <span class="progress-item">
                                        <el-progress :width="60" :show-text="false" type="circle" :percentage="25"></el-progress>
                                        <span class="progress-title">GPU</span>
                                        <span class="progress-text">1</span>
                                    </span>
                                </li>
                            </ul>
                            <h3 class="online-title">服务配置</h3>
                            <div class="editor-box"></div>
                        </div>

                        <el-dialog title="调用方式" :visible.sync="dialogFormVisible" >
                            <el-form :model="form">
                                <el-form-item label="访问地址" >
                                    <el-input  autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="Tocken" >
                                    <el-input  autocomplete="off"></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="dialogFormVisible = false">取 消</el-button>
                                <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
                            </div>
                        </el-dialog>
                    </el-tab-pane>
                    <el-tab-pane label="事件查看" name="second">
                        <el-table :data="incidentList">
                            <el-table-column  label="事件类型" >
                                <template slot-scope="scope">
                                    <span class="model-status" :class="'model-status-'+scope.row.type"></span>{{scope.row.type|typeZn}}
                                </template>
                            </el-table-column>
                            <el-table-column prop="info" label="事件信息"></el-table-column>
                            <el-table-column prop="time" label="事件发生时间" sortable></el-table-column>
                        </el-table>
                        <div class="pagination-line">
                            <el-pagination
                                @size-change="handleSizeChange"
                                background
                                @current-change="handleCurrentChange"
                                :current-page="currentPage4"
                                :page-sizes="[100, 200, 300, 400]"
                                :page-size="100"
                                layout="total, sizes, prev, pager, next, jumper"
                                :total="400">
                            </el-pagination>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="日志查看" name="third">
                        <div>
                            <el-input placeholder="请输入日志关键词" v-model="searchKey" size="small" style="width:250px;"></el-input>
                            <el-date-picker
                                v-model="value1"
                                type="datetimerange"
                                range-separator="至"
                                start-placeholder="开始日期"
                                size="small"
                                end-placeholder="结束日期">
                            </el-date-picker>
                            <el-button type="primary" size="small" style="width: 120px;">搜索</el-button>
                        </div>
                        <div class="log-box"></div>
                    </el-tab-pane>
                    <el-tab-pane label="监控信息" name="fourth">
                        <h3 class="online-title">资源使用情况</h3>
                        <div class="progress-item-div">
                            <el-progress type="circle" color="#6699ff" :percentage="75"></el-progress>
                            <p>CPU</p>
                            <ul>
                                <li><span class="square-icon" style="background: #6699ff;"></span><span>已使用 0.75</span></li>
                                <li><span class="square-icon"></span><span>可用 1</span></li>
                            </ul>
                        </div>
                        <div class="progress-item-div">
                            <el-progress type="circle" color="#52cca3" :percentage="19"></el-progress>
                            <p>内存</p>
                            <ul>
                                <li><span class="square-icon" style="background: #52cca3;"></span><span>已使用 0.75</span></li>
                                <li><span class="square-icon"></span><span>可用 1</span></li>
                            </ul>
                        </div>
                        <div class="progress-item-div">
                            <el-progress type="circle" color="#32c5ff" :percentage="75"></el-progress>
                            <p>GPU</p>
                            <ul>
                                <li><span class="square-icon" style="background: #32c5ff;"></span><span>已使用 0.75</span></li>
                                <li><span class="square-icon"></span><span>可用 1</span></li>
                            </ul>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="部署配置" name="fifth">
                        <h3 class="online-title">扩缩容</h3>
                        <p style="font-size: 13px;">模型部署占用资源：</p>
                        <p style="font-size: 13px;">
                            <span style="margin-right: 110px;"><span style="color:#FF3B30;margin-right:6px;">*</span>实例数</span>
                            <span><span style="color:#FF3B30;margin-right:6px;">*</span>Quota(1核、4GB内存)</span>

                        </p>
                        <div>
                            <el-input-number size="small" v-model="num" controls-position="right" @change="handleChange" :min="1" :max="10"></el-input-number>
                            <span style="margin: 0 10px;">X</span>
                            <el-input-number size="small" v-model="num2" controls-position="right" @change="handleChange" :min="1" :max="10"></el-input-number>
                            <span style="margin: 0 10px;">=</span>
                            <span style="color:#1677FF;">{{parseInt(num * num2)}}</span>
                            <span style="color: #666;margin: 0 10px;">Quota(1核、4GB内存)</span>
                        </div>
                        <div class="set-info">
                            <p><span class="el-icon-warning " style='color:#1677FF;margin-right: 4px;'></span>1Quota等于1核、4GB内存</p>
                            <p><span class="el-icon-warning " style='color:#1677FF;margin-right: 4px;'></span>对于高稳定性要求的正式服务，建议使用包含多台机器的资源组，并部署多个资源实例。</p>
                        </div>
                        <h3 class="online-title">服务降级</h3>
                        <table class="check-table">
                            <tr>
                                <td>服务名称</td>
                                <td>服务名称</td>
                                <td>操作</td>
                            </tr>
                            <tr>
                                <td>模型部署</td>
                                <td><span class="model-status model-status-1" :class="{'model-status-2':!checkForm.value1}"></span>{{checkForm.value1?'已开启':'已停止'}}</td>
                                <td>
                                    <el-switch
                                        v-model="checkForm.value1">
                                    </el-switch>
                                </td>
                            </tr>
                            <tr>
                                <td>服务日志</td>
                                <td><span class="model-status model-status-1" :class="{'model-status-2':!checkForm.value2}"></span>{{checkForm.value2?'已开启':'已停止'}}</td>
                                <td>
                                    <el-switch
                                        v-model="checkForm.value2">
                                    </el-switch>
                                </td>
                            </tr>
                            <tr>
                                <td>服务监控</td>
                                <td><span class="model-status model-status-1" :class="{'model-status-2':!checkForm.value3}"></span>{{checkForm.value3?'已开启':'已停止'}}</td>
                                <td>
                                    <el-switch
                                        v-model="checkForm.value3">
                                    </el-switch>
                                </td>
                            </tr>
                            <tr>
                                <td>在线调试</td>
                                <td><span class="model-status model-status-1" :class="{'model-status-2':!checkForm.value4}"></span>{{checkForm.value4?'已开启':'已停止'}}</td>
                                <td>
                                    <el-switch
                                        v-model="checkForm.value4">
                                    </el-switch>
                                </td>
                            </tr>
                        </table>
                    </el-tab-pane>
                </el-tabs>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "home",
        filters: {
            typeZn (val) {
                switch (val) {
                    case 1:
                        return '正常'
                    case 2:
                        return '失败'
                    case 3:
                        return '报警'
                }
            }
        },
        data () {
            return {
                checkForm: {
                    value1: true,
                    value2: true,
                    value3: false,
                    value4: false
                },
                num: '',
                num2: '',
                searchKey: '',
                value1: [],
                activeName: 'first',
                isStart: true,
                dialogFormVisible: false,
                form: {},
                currentPage4: 4,
                incidentList: [{
                    type: 1,
                    info: '模型部署成功并测试',
                    time: '2020-7-12 20:20:10'
                }, {
                    type: 2,
                    info: '生成可视化图表',
                    time: '2020-7-12 20:20:10'
                }, {
                    type: 3,
                    info: '事件发生时间',
                    time: '2020-7-12 20:20:10'
                }]
            }
        },
        methods: {
            handleChange(value) {
                console.log(value);
            },
            handleClick (tab, event) {
                console.log(tab, event);
            },
            changeStart () {
                this.isStart = !this.isStart
            },
            goDebug() {
                this.$router.push('/debug')
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
            }
        }
    }
</script>

<style scoped lang="scss">
    .bg{
        padding: 20px;
    }
    .onlineDetail-box{
        height: calc(100vh - 100px);
        background-color: #fff;
        border-top-left-radius: 50px;
        padding: 20px 30px;
        box-sizing: border-box;
        .back-line{
            font-size: 16px;
            color: #333;
            line-height: 40px;
        }
        .btn-line{
            padding-top: 4px;
            .el-button{
                width: 100px;
            }
        }
        .online-title{
            color: #333;
            font-size: 16px;
            margin: 14px 0 4px;
        }
        .online-ul{
            list-style: none;
            margin: 0;
            padding: 0 0 0 4px;
            li{
                line-height: 34px;
                font-size: 14px;
                color: #666;
            }
            .list-text{
                width: 12%;
                display: inline-block;
                vertical-align: top;
            }
            .list-content{
                width: 24%;
                display: inline-block;
                vertical-align: top;
            }
            .progress-item{
                text-align: center;
                width: 80px;
                display: inline-block;
                padding-top: 20px;
                margin-right: 40px;
                position: relative;
                span{
                    display: block;
                }
                .progress-title{
                    position: relative;
                    top: -16px;
                }
                .progress-text{
                    position: absolute;
                    top: 34px;
                    width: 80px;
                    left: 0;
                }
            }
        }
        .editor-box{
            margin-top: 10px;
            height: 140px;
            background: #f3f3f3;
        }
        .model-status{
            width: 8px;
            height: 8px;
            border-radius: 50%;
            display: inline-block;
            background-color: #666666;
            vertical-align: middle;
            margin-right: 6px;
        }
        .model-status-0{
            background-color: #DDDDDD;
        }
        .model-status-1{
            background-color: #52C41A;
        }
        .model-status-2{
            background-color: #FF3B30;
        }
        .model-status-3{
            background-color: #ebad18;
        }

        .pagination-line{
            background-color: #fff;
            padding: 20px 20px 10px;
            text-align: right;
        }
        .log-box{
            background: #f3f3f3;
            margin-top: 10px;
            height: calc(100vh - 280px);
        }
        .progress-item-div{
            width: 250px;
            text-align: center;
            position: relative;
            display: inline-block;
            padding-top: 30px;
            p{
                text-align: center;
            }
            ul{
                position: absolute;
                bottom: 0;
                right: 0;
                text-align: left;
                list-style: none;
                li{
                    font-size: 12px;
                    color: #999;
                    line-height: 24px;
                }
                .square-icon{
                    width: 10px;
                    height: 10px;
                    display: inline-block;
                    border-radius: 2px;
                    background: #e5e9f2 ;
                    margin-right: 4px;
                }
            }
        }
        .set-info{
            background: #E7F1FF;
            width: 550px;
            padding: 4px 20px;
            border-radius: 6px;
            font-size: 13px;
            margin-top: 10px;
            color: #666;
        }
        .check-table{
            width: 100%;
            border-collapse: collapse;
            tr:first-child td{
                background: #fafafa;
            }
            td{
                border-bottom: 1px solid #EEEEEE;
                padding: 10px 0;
                font-size: 14px;
                color: #333;
                text-align: center;
            }
        }
    }
</style>
<style>
    .onlineDetail-box .el-dialog{
        width: 500px;
    }
     .onlineDetail-box .el-table td{
         padding: 16px 0;
     }
    .onlineDetail-box .el-table th .cell{
        color: #666666;
    }
    .onlineDetail-box .el-table th{
        background-color: #fafafa;
        font-weight: normal;
    }
</style>



